.advantages__item-benefit .advantages__text {
  width: 215px;
  font-size: 1.4rem;
  line-height: 1.5;
  color: inherit;
  color: red;
}

.advantages__item-place {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 25px;
  padding-bottom: 30px;
  background-color: #eeeeee;
  color: red;
}

.advantages__item-place .advantages__title {
  width: 215px;
  display: block;
  font-size: 1.8rem;
  line-height: 1.5;
  font-weight: 700;
  text-align: center;
  color: inherit;
  margin-bottom: 8px;
}

.advantages__item-place .advantages__number {
  width: 60px;
  font-size: 1.4rem;
  line-height: 1.5;
  text-align: center;
  color: inherit;
  margin-bottom: 15px;
}

.advantages__item-place .advantages__text {
  width: 250px;
  font-size: 1.4rem;
  line-height: 1.5;
  text-align: center;
  color: inherit;
}

@media (min-width: 768px) {
  .advantages__inner {
    display: flex;
    flex-wrap: wrap;
  }
  .advantages__box  {
    width: 33.33%;
  }

  .advantages__box--triangle {
    padding-top: 45px;
    padding-bottom: 30px;
  }

  .advantages__box--triangle::before {
    background-image: none;
  }

  .advantages__box--bridge {
    padding-top: 45px;
  }

  .advantages__box .advantages__title {
    width: 145px;
    font-size: 2.1rem;
    line-height: 1.2;
    margin-bottom: 10px;
  }

  .advantages__box .advantages__number {
    margin-bottom: 20px;
  }

  .advantages__box .advantages__text {
    width: 175px;
  }

  .advantages__img {
    width: 66.66%;
    height: 256px;
  }

  .advantages__img--city {
   background-image: url("../img/main-city-desktop@1x.jpg");
  }

  @media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
    .advantages__img--city {
      background-image: url("../img/main-city-desktop@2x.jpg");
      background-size:  cover;
    }
  }

  .advantages__img--bridge {
   background-image: url("../img/main-bridge-desktop@1x.jpg");
   order: -1;
  }

  @media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
    .advantages__img--city {
      background-image: url("../img/main-bridge-desktop@2x.jpg");
      background-size:  cover;
    }
  }

  .advantages__list-benefit {
    width: 100%;
    padding-left: 0;
    display: flex;
    flex-wrap: wrap;
    padding-top: 0;
    padding-bottom: 0;
  }

  .advantages__item-benefit {
    width: 33.33%;
    align-items:center;
    margin-bottom: 0;
    padding-top: 160px;
    padding-bottom: 60px;
  }

  .advantages__item-benefit--present {
    order: 1;
  }

  .advantages__item-benefit .advantages__title {
    font-size: 2.1rem;
    line-height: 1.2;
    text-align: center;
    margin-bottom: 20px;
  }

  .advantages__title--house::before {
    width: 75px;
    height: 72px;
    top: 60px;
    left: calc(50% - 75px / 2);
    background-position: center center;
    background-size: 75px 72px;
    background-repeat: no-repeat;
    background-color: transparent;
  }

  .advantages__title--present::before {
    width: 74px;
    height: 70px;
    top: 60px;
    left: calc(50% - 74px / 2);
    background-position: center center;
    background-size: 74px 70px;
    background-repeat: no-repeat;
    background-color: transparent;
  }

  .advantages__title--food::before {
    width: 77px;
    height: 68px;
    top: 60px;
    left: calc(50% - 77px / 2);
    background-position: center center;
    background-size: 77px 68px;
    background-repeat: no-repeat;
    background-color: transparent;
  }

  .advantages__item-benefit .advantages__text {
    width: 170px;
    text-align: center;
  }

  .advantages__item-place {
    width: 33.33%;
    padding-top: 47px;
    padding-bottom: 42px;
  }

  .advantages__item-place .advantages__title {
    width: 115px;
    font-size: 2.1rem;
    line-height: 1.2;
    margin-bottom: 10px;
  }

  .advantages__item-place .advantages__number {
    margin-bottom: 23px;
  }

  .advantages__item-place .advantages__text {
    width: 160px;
  }
}

@media (min-width: 1200px) {
  .advantages__box .advantages__title {
    width: 155px;
    font-size: 2.1rem;
    line-height: 1;
    margin-bottom: 25px;
  }

  .advantages__box .advantages__text {
    width: 310px;
  }

  .advantages__img {
    background-position: center right;
    background-size: auto auto;
  }

  @media (min-width: 1200px) and (min-resolution: 144dpi), (min-width: 1200px) and (min-resolution: 1.5dppx) {
    .advantages__img--city {
      background-image: url("../img/main-city-desktop@2x.jpg");
    }
  }

  .advantages__item-benefit {
    padding-bottom: 80px;
  }

  .advantages__item-benefit .advantages__text {
    width: 285px;
  }

  .advantages__item-place {
    padding-bottom: 57px;
  }

  .advantages__item-place .advantages__title {
    width: 125px;
    line-height: 1;
    margin-bottom: 25px;
  }

  .advantages__item-place .advantages__text {
    width: 272px;
  }
}
